<template>
	<div class="formNotes">
		
		<form>	
			<div class="menu">
				<button type="button" @click="RemoveNote" class="bg-danger btn btn-delete">Delete</button>
				<button type="submit" @click="saveNote" class="bg-success btn" v-if="mode == 'save'">Save</button>
				<button type="submit" @click="UpdateNote" class="bg-success btn" v-if="mode == 'update'">Update</button>
			</div>

			<div class="content">
				<input type="text" class="text" name="IsiText" placeholder="id" v-model="id">
				<input type="text" class="text" name="IsiText" placeholder="Title" v-model="title">
				<textarea class="text textarea" placeholder="Masukan disini" v-model="description"></textarea>
			</div>
		</form>

	</div>
</template>

<script type="text/javascript">

	export default {
		name:'listNotes',
		props: {
			propSaveNote: {
				type: Function
			},
			propNoteObjek: {
				type: Object
			},
			propUpdateNote: {
				type: Function
			},
			propRemoveNote: {
				type: Function
			}
		},
		data: function() {
			return{
				id: 0,
				title: '',
				description: '',
				mode: 'save'
			}
		},
		methods: {
			saveNote(e){
				e.preventDefault();

				this.propSaveNote(this.title, this.description);
			},
			UpdateNote(e){
				e.preventDefault();

				this.propUpdateNote(this.id, this.title, this.description);

			},
			RemoveNote(e){
				e.preventDefault();

				this.propRemoveNote(this.id);
				this.clearConsole();
			},
			clearConsole(){
				this.id = 0;
				this.title = '';
				this.description = '';
			}
		},
		watch: {
			propNoteObjek: function(note){
				this.id = note.id
				this.title = note.title;
				this.description = note.description;
				this.mode = note.mode;
			}
		}
	}
</script>

<style>
.menu{     
	background: #f7f7f7;     
	padding:10px 25px;     
	margin-bottom: 25px;     
	text-align:right;     
	border-bottom: 1px solid #e8e6e6;
}

.btn-delete{ 
	margin-right:10px; 
}

.content{     
	padding: 0px 25px;
}

.text{     
	display: block;     
	width: 100%;     
	padding: 0px;     
	font-size: 20px;     
	font-weight: bold;     
	color: #2c3e50;     
	border: none;     
	margin-bottom: 10px;     
	box-sizing: border-box;     
	outline: none;
}

.textarea{     
	min-height: 350px;     
	font-size: 15px;     
	font-weight: lighter;     
	line-height: 30px;
}

.loader{     
	vertical-align: middle;
}
</style>