<template>
  <div id="app">
    <div class="kiri">
      <div class="logo">
        <a href="#">
          <h2>Notes</h2>
          <span>Damar</span>
        </a>
      </div>
      <div class="frame-notes">
          <button @click="newNote" class="bg-success btn btn-new-note">
            + Note baru
          </button>

          <listNotes :proplistNote="notes" :propEditNote="editNote" />
      </div>
    </div>
    <div class="kanan">
      <!-- Form list -->
          <formNotes :propRemoveNote="RemoveNote" :propSaveNote="saveNote" :propUpdateNote="updateNote" :propNoteObjek="dataNote" />
    </div>
  </div>
</template>

<script>
import listNotes from './components/listNotes.vue'
import formNotes from './components/formNotes.vue'
// import HelloWorld from './components/HelloWorld.vue'
import axios from 'axios';


export default {
  name: 'App',
  data: function() {
      return{
        dataNote: {},
        notes: [
        // {id:1, tittle:'Notes Damar', description:'Ini adalah isi'}, 
        // {id:2, tittle:'Notes', description:'Ini adalah kamu'}
      ]
    }
  },
  components: {
    listNotes,
    formNotes
  },
  methods: {
    newNote(){

      this.dataNote = {id: 0, title: '', description: '', mode: 'save'}
    },
    editNote(id){

      this.dataNote = this.notes.find(note => note.id === id);
      this.dataNote.mode = 'update';

    },
    saveNote(title, description){

      let params = new URLSearchParams();
        params.append('title', title);
        params.append('description', description);
        axios.post('http://localhost/project-notes/note/create', params).then(response => {

          let data = {
            id: response.data.id,
            title: title,
            description: description
          }
          
          this.notes.unshift(data);
          this.editNote(data.id);
      });
    },
    updateNote(id, title, description){


      let params = new URLSearchParams();
        params.append('id', id);
        params.append('title', title);
        params.append('description', description);
        axios.post('http://localhost/project-notes/note/update', params).then(response => {

          let updatnotes = this.notes.findIndex(note => note.id === id);
          this.notes[updatnotes].title = title;
          this.notes[updatnotes].description = description;
          
          return response;
      });
      
    },
    RemoveNote(id){

      let params = new URLSearchParams();
        params.append('id', id);
        axios.post('http://localhost/project-notes/note/delete/', params).then(response => {

        let removenotes = this.notes.findIndex(note => note.id == id);
        this.notes.splice(removenotes, 1);
          
        return response;
      })
      .catch(function (error) {
        console.log(error);
      });

    },
    getdata(){
        axios.get('http://localhost/project-notes/note').then(response => {
          this.notes = response.data;
        });
    }
  },
  created(){
    this.getdata();
  }
}
</script>

